<template>
    <div class="clients__wrapper">
        <ClientCardMini v-for="client in clients" :key="client.phone"/>
        <bottom-menu active="clients"/>
    </div>
</template>
<script>
import ClientCardMini from '../components/ClientCardMini.vue'
import BottomMenu from '../components/BottomMenu.vue'
export default {
    components: {ClientCardMini, BottomMenu},
    data(){
        return{
            clients: []
        }
    },
    created(){
        this.clients = JSON.parse(localStorage.getItem('users'))
    }
}
</script>
<style lang="scss" scoped>
.clients__wrapper{
    min-height: 100vh;
    background: #E5E5E5;
}
</style>