<template>
    <div class="singleDay__main" ref="main">
        <Spinner :start="start"/>
        <MainHeader filterType = "oneDay" :options="options" :initialValue="initialValue" @changed="changed"/>
        <div class="content">
            <DateHeader :date="day" :buttonNeeded="true" @prev='prev' @next='next'/>
            <Timetable :id="id" :date="day" :infoAboutDay="setInfo(day.day())"  :key="day.toString()" @move="move" :bookings="bookings"/>
        </div>
        <div class="bookings">
            <div class="bookings__title">
                <p>: {{bookings.length}}</p>
            </div>
            <div v-for="booking, index in bookings" :key="index" class="bookings__content">
                <BookingMiniature :info="booking" @addPayload="addPayload" />
            </div>
        </div>
        <BottomMenu active="calendar"/>
        <payment-popup v-if="openPopup" @addPayment="addMoney" @closePayment="closePayment" :remain="remainForBooking">
            <template v-slot:title>
                <p> </p>
            </template>    
        </payment-popup> 
    </div>
</template>
<script>
import dayjs from 'dayjs'
import 'dayjs/locale/ru';
import MainHeader from '../components/MainHeader.vue'
import Timetable from '../components/Timetable.vue'
import DateHeader from '../components/DateHeader.vue'
import BookingMiniature from '../components/BookingMiniature.vue'
import PaymentPopup from '../components/PaymentPopup.vue'
import BottomMenu from '../components/BottomMenu.vue'
import Spinner from '../components/Spinner.vue';
import {mapState} from 'vuex';
import axios from 'axios'
export default {
    data(){
        return {
            day: dayjs().locale('ru'),
            bookings: [],
            openPopup: false,
            oId: '',
            bId: '',
            id: 7,
            remainForBooking: 0,
            start: false,
            options: [],
            initialValue: {},
            timelines: ["05:00", "05:30", "06:00", "06:30", "07:00", "07:30", "08:00", "08:30", "09:00", "09:30", "10:00", "10:30", "11:00", "11:30", "12:00", "12:30", "13:00", "13:30", "14:00", "14:30", "15:00", "15:30", "16:00", "16:30", "17:00", "17:30", "18:00", "18:30", "19:00", "19:30", "20:00", "20:30", "21:00", "21:30", "22:00", "22:30", "23:00", "23:30", "00:00", "00:30", "01:00", "01:30", "02:00", "02:30", "03:00", "03:30", "04:00", "04:30"]            
        }
    },
    watch: {
        day: async function(){
            await this.getDailyOrders();
        }, 
        'facility.id': async function(){
            await this.getDailyOrders();   
        } 
              
    }, 
    async mounted(){
        this.start = true;
        await this.$store.dispatch('facility/getAllFacilities').then(res=>{
            const result = res.data.user.facilities;
            this.options.push(...result.map(e=>{return {value: e.id, text: e.fc_name}}));
            this.initialValue = {
                'value': parseInt(this.facility.id),
                'text': result.find(e=>parseInt(e.id)===parseInt(this.facility.id)).fc_name
            }
        })
        this.getDailyOrders();
        window.onbeforeunload = function () {
            window.scrollTo(93, 0);
        }
    }, 
    methods:{
        move(l){
            this.$refs['main'].scrollLeft+=l*4/5;
        },
        setInfo(dayOfWeek){
            let infoFieldsDaily = {}
            this.getFacility.forEach(e=>{
                infoFieldsDaily = {...infoFieldsDaily, [e.id]:e.periods_data[dayOfWeek]}
            })
            return infoFieldsDaily
        },
        async getDailyOrders(){
            this.start = true;
            await axios.get(`https://almvtst.ml/crm/user/${sessionStorage.getItem('id')}/facility/${this.facility.id}/${this.day.format('DD-MM-YYYY')}`, {
                headers: {'Authorization': `Bearer ${sessionStorage.getItem('access_token')}`}
            }).then(res=>this.bookings = res.data.bookings);
            this.start = false;
        },
        prev(){
            this.day = this.day.subtract(1,'day');
            this.$router.push({name:'day', params: {id: this.day.format('DD-MM-YYYY')} })   
            
        },
        next(){
            this.day = this.day.add(1,'day');
            this.$router.push({name:'day', params: {id: this.day.format('DD-MM-YYYY')} })
        },
        addPayload(oId, bId, r){
            this.bId = bId
            this.oId = oId
            this.openPopup = true
            this.remainForBooking = r
        },
        closePayment(){
            this.openPopup = false
        },
        addMoney(money){
            for(const order in this.orders){
                if(this.oId===this.orders[order].orderId){
                    for(const booking in this.orders[order]['bookings']){
                        if(this.orders[order]['bookings'][booking].bookingId===this.bId){
                            this.orders[order]['bookings'][booking].paid+=money
                            break;
                        }
                    }
                }
            }
            localStorage.setItem('orderInfo', JSON.stringify(this.orders))
            this.openPopup = false
        },
        changed(id){
            this.$store.dispatch('facility/resetFacility', parseInt(id));
        }
    },
    components: {
      Timetable, 
      MainHeader, 
      DateHeader,
      BookingMiniature,
      PaymentPopup,
      BottomMenu,
      Spinner
    },
    computed:{
        getSingleDay: function(){
            return this.$route.params.id.toString();
        },
        getFacility(){
            return this.facility.playfields.filter(e=>!e.periods_data[this.day.day()].dayoff)
        },
        ...mapState('admin', ['user']),
        ...mapState('facility', ['facility'])
    }
    
}
</script>
<style lang="scss" scoped>
.singleDay__main{
    overflow: auto;
    min-height: 100vh;
    background: #E5E5E5;
    margin-bottom: 49px;
}
.content{
    width: max-content;
    min-width: 2487px;
    padding-top: 93px;
}
.bookings{
    position: sticky;
    left: 0;
    right: 0;
    width: 100vw;
}
.bookings::-webkit-scrollbar{
    width: 0;
}
.bookings__title{
    padding: 16px 12px;
    background: #fff;
    height: 52px;
    p{
        font-family: 'Roboto', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
    }
}
</style>
